.bus-stop-card-container {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.082);
}

.bus-stop-card {
    cursor: pointer;
    padding: 20px;
    border-radius: 3px;
}

.bus-stop-card > h1 {
    font-size: 3rem;
    font-weight: 300;
}

.bus-stop-card > h3 {
    font-weight: 700;
    margin: 5px 0;
}

.bus-stop-card > h4 {
    font-size: 1.2rem;
    font-weight: 700;
}


.bus-stop-card__controls {
    display: flex;
    justify-content: space-between;
}


.refresh-icon {
    font-size: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px;
    transition: transform 0.1s linear;
    color: rgb(81, 131, 189);
    cursor: pointer;
}


.refresh-icon:hover {
    transform: rotate(30deg);
}

.spin {
    animation: spin 0.7s linear infinite;
}


.filler-box {
    cursor: pointer;
    flex: 1;
}


@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}