@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap');

:root {
  --primary-color: #333;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


html {
  font-size: 62.5%;
}


body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  color: var(--primary-color);
}


button {
  display: inline-block;
  padding: 10px 15px;
  text-transform: uppercase;
  color: #fff;
  outline: none;
  border: none;
}


.blue {
  background-color: rgb(66, 126, 161);
  background-image: radial-gradient(circle, transparent 1%, rgb(132, 173, 211) 1%);
}

.orange {
  background-color: orange;
  background-image: radial-gradient(circle, transparent 1%, rgb(245, 196, 128) 1%);
}

.ripple {
  background-position: center;
  background-size: 12000%;
  transition: background 0.4s;
}

.ripple:active {
  background-size: 100%;
  transition: background 0s;
}