.spinner-container {
    height: 90vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner {
    height: 50px;
    width: 50px;
    border: 3px solid #ccc;
    border-right: 3px solid #777;
    border-radius: 50%;
    animation: spin 0.6s linear infinite;
}


@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}