nav {
    display: flex;
    justify-content: space-between;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
    user-select: none;
}

nav .logo-box {
    margin: 15px;
}

nav .logo-box a {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 30px;
    color: #4a4a4a;
}

nav ul {
    list-style: none;
    display: flex;
}


nav ul li {
    padding: 25px 30px 10px;
}

nav ul li a {
    display: inline-block;
    text-decoration: none;
    text-transform: uppercase;
    padding: 2px;
    color: #777;
    border-bottom: 2px solid transparent;
}


nav ul li a:hover {
    border-bottom: 2px solid orange;
}


.nav-toggle {
    display: flex;
}


.nav-menu {
    position: absolute;
    top: 0;
    right: 0;
    padding: 22px;
    cursor: pointer;
    display: none;
}


.nav-menu__icon {
    display: inline-block;
    height: 2px;
    width: 18px;
    background-color: #000;
    position: relative;
}


.nav-menu__icon::before {
    content: "";
    height: 100%;
    width: 100%;
    background-color: #000;
    position: absolute;
    top: -5px;
}


.nav-menu__icon::after {
    content: "";
    height: 100%;
    width: 100%;
    background-color: #000;
    position: absolute;
    top: 5px;
}

.nav-menu__icon--active {
    background-color: transparent;
    transition: all 0.4s;
}

.nav-menu__icon--active::before {
    top: 0;
    transform: rotate(45deg);
    transition: all 0.4s;
}

.nav-menu__icon--active::after {
    top: 0;
    transform: rotate(-45deg);
    transition: all 0.4s;
}


@media only screen and (max-width: 768px) {
    nav,
    nav ul {
        flex-direction: column;
    }

    nav ul li {
        text-align: center;
    }

    nav ul li:last-child {
        padding-bottom: 30px;
    }

    .nav-toggle {
        display: none;
    }

    .nav-menu {
        display: block;
    }
}