.arrival-card {
    border-radius: 3px;
    margin: 10px;
    padding: 0 20px 0 10px;
}

.arrival-card__bus-stop-code::after {
    content: "";
    display: block;
    width: 70px;
    height: 2px;
    background-color: orange;
    margin: 5px auto;
}


.arrivals-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
}

span.arrival-card__service-no {
    font-size: 3rem;
    font-weight: 300;
    width: 50px;
    text-align: end;
    cursor: pointer;
    transition: transform 0.3s linear;
}

span.arrival-card__service-no:hover {
    transform: translateY(-3px);
}

.arrival-card__additional-info-container {
    padding: 30px;
}

.arrival-card__additional-info-container h2 {
    font-weight: 300;
    font-size: 50px;
    margin-bottom: 20px;
}

.arrival-card__additional-info {
    display: flex;
    justify-content: space-between;
}

.arrival-card__additional-info > p {
    margin: 5px 0;
    padding: 20px 10px 10px 0;
}

.arrival-card__additional-info--bus-name {
    width: 50%;
}

.arrival-card__additional-info--bus-stop-code {
    font-weight: 300;
    font-size: 25px;
}